.App {
  text-align: center;
  font-family: 'Jameel Noori Nastaleeq Regular';
  direction: rtl;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.title{
  display: flex;
  align-items: center;
  text-align: center;
    height:5rem;
    /* background:#155768; */
    border:none;
   margin-top: 0rem;
    box-shadow: 0 .3rem 1rem rgba(0,0,0,.3);
    align-items: center;
    justify-content: center;
    /* direction: rtl; */
}

h1{
  color:#fff;
  font-size: 2.5rem;
  text-align: center;
}

button{
  font-family: 'Jameel Noori Nastaleeq Regular';

  height:4rem;
  width:20rem;
  padding: 0;
  background:#155768;
  color:#fff;
  border:none;
  border-radius: 1rem;
  box-shadow: 0 .3rem 1rem rgba(0,0,0,.3);
  cursor: pointer;
  font-size: 1.2rem;
  transition: .2s;
  margin:.5rem .5rem;
  direction: rtl;
}

span{
  margin: 5rem;
}

button:hover{
  background-color:#FFC300 ;
  color:#282c34 ;

}


.makhraj{
  display: flex;
  align-items: center;
  justify-content: center;
}
.makhraj h1{
  flex-basis: 27%;
  height: 4rem;
  border-radius: 3rem;
  box-shadow: 0 .3rem .8rem rgba(33, 252, 44, 0.932);
    font-size: 2rem;
    color:#fff;
    border:none;
}

.makhraj h2{
  flex-basis: 22%;
  column-gap: 2rem;
  border-radius: .5rem;
  box-shadow: 0 .3rem .8rem rgba(0, 255, 13, 0.5);
    font-size: 1.4rem;
    color:#FFC300 ;
    border:none;
    padding: .5rem .1rem;
    margin:.5rem .5rem 1rem;
}

.makhraj span{
  margin: .8rem;
}



@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
